@font-face{
font-family:"gen-normal" ;
src: url('https://cdn.leafscape.be/SourceHanSansJP/SourceHanSansJP-Normal_web.woff')
     format("woff");
}
@font-face{
font-family:"gen-bold" ;
src: url('https://cdn.leafscape.be/SourceHanSansJP/SourceHanSansJP-Bold_web.woff')
     format("woff");
}

$white: #F4F4F4;
$black: #333333;
$purple: rgb(51,41,79);
$grey-dark: #6d6d6d;
$l_yellow: rgb(161, 143, 62);

$line: 2px solid $black;

$margin-lm: 30px;
$margin-m: 22px;
$margin-s: 8px;

$font-xl: 1.6rem;
$font-l: 1.3rem;
$font-lm: 1.2rem;
$font-m: 1rem;
$font-ms: 0.9rem;
$font-s: 0.8rem;
$font-ss: 0.5rem;

$width: 90%;
$transition: .1s;

@mixin line {
  border: 1px solid $black;
}

@mixin hover_opacity {
  transition: $transition;
  &:hover{
    opacity: 0.8;
    @content;
  }
}

@mixin width_margin($LR, $margin, $L-ratio, $R-ratio){
  @if $LR == 'Right' or $LR == 'R' {
    margin-left: $margin;
    $width: 10 * $R-ratio;
    width: calc(#{$width}% - #{$margin});
  }
  @else if $LR == 'Left' or $LR == 'L' {
    margin-right: $margin;
    $width: 10 * $L-ratio;
    width: calc(#{$width}% - #{$margin});
  }
}
@mixin clear_width_margin($LR){
  @if $LR == 'Right' or $LR == 'R' {
    margin-left: 0;
    width: 100%;
  }
  @else if $LR == 'Left' or $LR == 'L' {
    margin-right: 0;
    width: 100%;
  }
}

@mixin background_dark($color) {
  background-color: $color;
  color: $white;
}

@mixin background_black() {
  @include background_dark($black);
}

@mixin background_purple {
  @include background_dark($purple);
}

@mixin not_last_child {
  *:not(:last-child){
    @content;
  }
}

@mixin sp {
  @media screen and(max-width: 750px) {
    @content;
  }
}

@mixin pc {
  @media screen and(min-width: 751px) {
    @content;
  }
}