@import '_var';

.container{
  main{
    line-height: 1.4rem;
    .wrap_cats-date{
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      section.date{
        margin: 22px 0;
      }
    }
    section.content{
      *{
        margin-bottom: $margin-lm;
      }
      a{
        color: #4C799A;
        text-decoration: underline;
        @include hover_opacity();
      }
      h1{
        font-weight: bold;
        font-size: $font-xl;
        @include background_purple();
      }
      h2{
        color: $purple;
        font-family: 'gen-bold';
        font-size: $font-l;
        font-weight: bold;
      }
      h3{
        font-size: $font-lm;
        font-family: 'gen-bold';
        font-weight: bold;
      }
      h4{
        color: $l_yellow;
        font-family: 'gen-bold';
        font-weight: bold;
      }
      p{
        margin-bottom: $margin-m;
      }
      ul{
        li{
          margin-bottom: $margin-s;
          &::before{
            content: "ãƒ» ";
          }
        }
      }
      ol{
        counter-reset: item;
        li{
          margin-bottom: $margin-s;
          &:before {
            counter-increment: item;
            content: counter(item)'. ';
            font-size: $font-m;
          }
        }
      }
      img{
        width: 100%;
        height: auto;
      }
    }
    section.share{
      display: flex;
      justify-content: flex-end;
      @include not_last_child{
        margin-right: 10px;
      }
      *{
        width: 100px;
      }
    }
  }
  section.side_headers{
    margin-top: $margin-m;
    font-size: $font-ms;
    &.active{
      top: $margin-m;
      right: 4.5%;
      margin-top: 0;
      width: calc(90vw * .35 - 22px);
    }
    @for $i from 2 through 4{
      .h#{$i}{
        margin-left: calc(#{$i - 2} * #{$margin-lm});
        margin-bottom: $margin-s;
        @include hover_opacity(){
          text-decoration: underline;
        }
      }
    }
  }
}